<template>
	<view>
		<view style="
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		background-color: #fff;
		padding: 30rpx;
		box-sizing: border-box;
		z-index: 999;
		">
			<!--分段器：互相关注、关注、被关注-->
			<view class="ff-box">
				<view class="in-ff-box" :style="pageDict == 'mutual' ? 'color: #333;font-weight:bold' : ''"
					@click="subChange('mutual')">
					<text class="text-in-ff-box">互相关注</text>
				</view>
				<view class="in-ff-box" :style="pageDict == 'following' ? 'color: #333;font-weight:bold' : ''"
					@click="subChange('following')">
					<text class="text-in-ff-box">关注</text>
				</view>
				<view class="in-ff-box" :style="pageDict == 'followers' ? 'color: #333;font-weight:bold' : ''"
					@click="subChange('followers')">
					<text class="text-in-ff-box">被关注</text>
				</view>
			</view>
		</view>
		<view class="box">
			<view style="height: 100rpx;"></view>

			<view v-if="pageDict == 'following'">
				<view v-for="(item, index) in bodyDataOfFollowing.content">

					<user-card :ovoUserInfo="item.followOvoUser"
						@click="$refs.userPopup.open(item.followOvoUser)"></user-card>
					<view style="height: 20rpx;"></view>
				
				</view>

				<view v-if="bodyDataOfFollowing.content.length == 0 && bodyDataOfFollowingEnd" class="box-of-no-have">
					<image class="icon-of-no-have" :src="$kt.file.byPath('image/icon/noHave.svg')" />
					<view>没有关注的人~</view>
				</view>

				<view class="push-btn btn" v-if="!bodyDataOfFollowingEnd && !bodyDataOfFollowingLoading"
					@click="getFollowingListSelf()">查看更多</view>
				<view class="push-btn" v-if="bodyDataOfFollowingLoading && followingNum >= 0">正在加载中...</view>
				<view class="push-btn" v-if="bodyDataOfFollowingEnd && bodyDataOfFollowing.content.length > 0">没有更多了~
				</view>
			</view>

			<view v-if="pageDict == 'followers'">
				<view v-for="(item, index) in bodyDataOfFollowers.content">
					<user-card @click="$refs.userPopup.open(item.ovoUser)" :ovoUserInfo="item.ovoUser"></user-card>
					<view style="height: 20rpx;"></view>
				</view>

				<view class="push-btn btn" v-if="!bodyDataOfFollowersEnd && !bodyDataOfFollowersLoading"
					@click="getFollowerListSelf()">查看更多</view>

				<view v-if="bodyDataOfFollowers.content.length == 0 && bodyDataOfFollowersEnd" class="box-of-no-have">
					<image class="icon-of-no-have" :src="$kt.file.byPath('image/icon/noHave.svg')" />
					<view>暂时没有被关注~</view>
				</view>

				<view class="push-btn" v-if="bodyDataOfFollowersLoading && followersNum >= 0">正在加载中...</view>
				<view class="push-btn" v-if="bodyDataOfFollowersEnd && bodyDataOfFollowers.content.length > 0">没有更多了~</view>
			</view>

			<view v-if="pageDict == 'mutual'">
				<view v-for="(item, index) in bodyDataOfMutual.content">
					<user-card @click="$refs.userPopup.open(item.ovoUser)" :ovoUserInfo="item.ovoUser"></user-card>
					<view style="height: 20rpx;"></view>
				</view>

				<view v-if="bodyDataOfMutual.content == 0 && bodyDataOfMutualEnd" class="box-of-no-have">
					<image class="icon-of-no-have" :src="$kt.file.byPath('image/icon/noHave.svg')" />
					<view>暂时没有互相关注~</view>
				</view>

				<view class="push-btn btn" v-if="!bodyDataOfMutualEnd && !bodyDataOfMutualLoading"
					@click="getMutualListSelf()">查看更多</view>
				<view class="push-btn" v-if="bodyDataOfMutualLoading && mutualNum >= 0">正在加载中...</view>
				<view class="push-btn" v-if="bodyDataOfMutualEnd && bodyDataOfMutual.content.length > 0">没有更多了~</view>

			</view>

		</view>

		<user-popup ref="userPopup" @unFollow="unFollow" @follow="follow"></user-popup>
	</view>
</template>

<script>
export default {
	data() {
		return {

			pageDict: "following",
			requestParamOfFollowing: {
				pageNumber: 1,
				sortField: "gmtCreate",
				sortOrderBy: "desc",
			},
			bodyDataOfFollowing: {
				content: []
			},
			bodyDataOfFollowingEnd: false,
			bodyDataOfFollowingLoading: false,

			requestParamOfFollowers: {
				pageNumber: 1,
				sortField: "gmtCreate",
				sortOrderBy: "desc",
			},
			bodyDataOfFollowers: {
				content: []
			},
			bodyDataOfFollowersEnd: false,
			bodyDataOfFollowersLoading: false,

			requestParamOfMutual: {
				pageNumber: 1,
				sortField: "gmt_create",
				sortOrderBy: "desc",
			},
			bodyDataOfMutual: {
				content: []
			},
			bodyDataOfMutualEnd: false,
			bodyDataOfMutualLoading: false,
			isMe: false,
			followingNum: -1,
			followersNum: -1,
			mutualNum: -1
		}
	},
	onLoad(options) {
		this.pageDict = options.type;
		this.isMe = options.isMe == 1;
		this.getData();
		// 有人取消关注
		uni.$on("reduceFollowers", (data) => {
			console.log("reduceFollowers", data);
			let index = this.bodyDataOfFollowers.content.findIndex(item => item.ovoUser.userId == data.userId);
			if (index != -1) {
				uni.showToast({
					title: data.user.nickname + " 对你取消了关注",
					icon: "none"
				});
				this.bodyDataOfFollowers.content.splice(index, 1);

			}
		});
	},

	methods: {
		unFollow(ovoUser) {
			console.log("unFollow", ovoUser);
			if(!this.isMe){
				return false;
			}

			// 寻找对应的用户
			let index = this.bodyDataOfFollowing.content.findIndex(item => item.followOvoUser.userId == ovoUser.userId);
			if (index != -1) {
				this.bodyDataOfFollowing.content.splice(index, 1);
				// 查看对应的缓存
				let followingList = uni.getStorageSync("followingListSelf");
				if (followingList) {
					let index2 = followingList.findIndex(item => item.ovoUser.userId == ovoUser.userId);
					if (index2 != -1) {
						followingList.splice(index2, 1);
						uni.setStorageSync("followingListSelf", followingList);
					}
				}
			}
			// 寻找对应的互相关注的用户
			let index2 = this.bodyDataOfMutual.content.findIndex(item => item.ovoUser.userId == ovoUser.userId);
			if (index2 != -1) {
				this.bodyDataOfMutual.content.splice(index2, 1);
				// 查看对应的缓存
				let mutualList = uni.getStorageSync("mutualListSelf");
				if (mutualList) {
					let index3 = mutualList.findIndex(item => item.ovoUser.userId == ovoUser.userId);
					if (index3 != -1) {
						mutualList.splice(index3, 1);
						uni.setStorageSync("mutualListSelf", mutualList);
					}
				}
			}
			if (this.pageDict == "following" || this.pageDict == "mutual") {
				this.$refs.userPopup.close();
			}
		},
		follow(ovoUser) {
			if(!this.isMe){
				return false;
			}

			// 寻找对应的用户
			let index = this.bodyDataOfFollowing.content.findIndex(item => item.ovoUser.userId == ovoUser.userId);
			if (index == -1) {
				this.bodyDataOfFollowing.content.unshift({
					followOvoUser: ovoUser,
					gmtCreate: new Date().getTime(),
				});
				// 查看关注的缓存
				let followingList = uni.getStorageSync("followingListSelf");
				if (followingList) {
					followingList.unshift({
						followOvoUser: ovoUser,
						gmtCreate: new Date().getTime(),
					});
					uni.setStorageSync("followingListSelf", followingList);
				}
			}
			// 寻找对应的互相关注的用户
			let index2 = this.bodyDataOfMutual.content.findIndex(item => item.ovoUser.userId == ovoUser.userId);
			// 寻找对应的粉丝
			let index3 = this.bodyDataOfFollowers.content.findIndex(item => item.ovoUser.userId == ovoUser.userId);
			if (index2 == -1 && index3 != -1) {
				this.bodyDataOfMutual.content.unshift({
					ovoUser: ovoUser,
					gmtCreate: new Date().getTime(),
				});
				// 查看互相关注的缓存
				let mutualList = uni.getStorageSync("mutualListSelf");
				if (mutualList) {
					mutualList.unshift({
						ovoUser: ovoUser,
						gmtCreate: new Date().getTime(),
					});
					uni.setStorageSync("mutualListSelf", mutualList);
				}

			}

			this.$refs.userPopup.close();
		},
		subChange(pageDict) {
			this.pageDict = pageDict;
			this.getData();
		},
		/**
		 * 获取数据
		 */
		getData() {
			if (this.pageDict == "following") {
				this.followingNum++;
				if (this.followingNum > 0) {
					return null;
				}
				if (this.isMe) {
					this.getFollowingListSelf();
				}
			}
			if (this.pageDict == "followers") {
				this.followersNum++;
				if (this.followersNum > 0) {
					return null;
				}
				if (this.isMe) {
					this.getFollowerListSelf();
				}
			}
			if (this.pageDict == "mutual") {
				this.mutualNum++;
				if (this.mutualNum > 0) {
					return null;
				}
				if (this.isMe) {
					this.getMutualSelf();
				}
			}
		},
		getFollowingListSelf() {
			if (this.bodyDataOfFollowingLoading) {
				return null;
			}

			// 先从缓存中取
			var bodyDataOfFollowingContent = uni.getStorageSync("followingListSelf");
			// 如果缓存中有数据
			if (bodyDataOfFollowingContent) {
				this.bodyDataOfFollowing.content = bodyDataOfFollowingContent;
			}

			this.bodyDataOfFollowingLoading = true;
			this.$kt.request.request({
				api: this.$api.business.ovo.userBind.getFollowingListSelf,
				data: this.requestParamOfFollowing,
				stateSuccess: res => {
					if (this.followingNum == 0) {
						this.bodyDataOfFollowing.content = []
					}

					var content = res.data.content;
					// 删除与this.bodyDataOfFollowing.content重复的数据
					content = content.filter(item => {
						let index = this.bodyDataOfFollowing.content.findIndex(item2 => item2.followOvoUser.id == item.followOvoUser.id);
						if (index == -1) {
							return true;
						}
						return false;
					});



					this.bodyDataOfFollowing.content = this.bodyDataOfFollowing.content.concat(content);

					if (this.bodyDataOfFollowing.content.length != 0) {
						// 将this.bodyDataOfFollowing.content都循环一边，并将item都加一个属性isStorage
						var bodyDataOfFollowingContent = JSON.parse(JSON.stringify(this.bodyDataOfFollowing.content.slice(0, 100)));
						for (var i = 0; i < bodyDataOfFollowingContent.length; i++) {
							bodyDataOfFollowingContent[i].isStorage = true;
						}
						uni.setStorageSync("followingListSelf", bodyDataOfFollowingContent);

					}else{
						uni.removeStorageSync("followingListSelf");
					}

					if (res.data.content.length == 0) {
						this.bodyDataOfFollowingEnd = true;
						this.bodyDataOfFollowingLoading = false;
						return false;
					}

					if (res.data.content.length < res.data.pageSize) {
						this.bodyDataOfFollowingEnd = true;
						this.bodyDataOfFollowingLoading = false;
						return false;
					}
					this.followingNum++;
					this.requestParamOfFollowing.pageNumber++;
					this.bodyDataOfFollowingLoading = false;

				}
			})
		},
		getFollowerListSelf() {
			if (this.bodyDataOfFollowersLoading) {
				return null;
			}

			// 先从缓存中取
			var bodyDataOfFollowersCntent = uni.getStorageSync("followerListSelf");
			// 如果缓存不为空
			if (bodyDataOfFollowersCntent) {
				this.bodyDataOfFollowers.content = bodyDataOfFollowersCntent;
			}


			this.bodyDataOfFollowersLoading = true;
			this.$kt.request.request({
				api: this.$api.business.ovo.userBind.getFollowerListSelf,
				data: this.requestParamOfFollowers,
				stateSuccess: res => {
					if (this.followersNum == 0) {
						this.bodyDataOfFollowers.content = []
					}

					this.bodyDataOfFollowers.content = this.bodyDataOfFollowers.content.concat(res.data.content);

					if (this.bodyDataOfFollowers.content.length != 0) {
						// 将this.bodyDataOfFollowers.content都循环一边，并将item都加一个属性isStorage
						var bodyDataOfFollowersContent = JSON.parse(JSON.stringify(this.bodyDataOfFollowers.content.slice(0, 100)));
						for (var i = 0; i < bodyDataOfFollowersContent.length; i++) {
							bodyDataOfFollowersContent[i].isStorage = true;
						}
						uni.setStorageSync("followerListSelf", bodyDataOfFollowersContent);
					}else{
						uni.removeStorageSync("followerListSelf");
					}

					if (res.data.content.length == 0) {
						this.bodyDataOfFollowersEnd = true;
						this.bodyDataOfFollowersLoading = false;
						return false;
					}
					if (res.data.content.length < res.data.pageSize) {
						this.bodyDataOfFollowersEnd = true;
						this.bodyDataOfFollowersLoading = false;
						return false;
					}
					this.followersNum++;
					this.requestParamOfFollowers.pageNumber++;
					this.bodyDataOfFollowersLoading = false;
				}
			})
		},

		getMutualSelf() {
			if (this.bodyDataOfMutualLoading) {
				return null;
			}
			// 先从缓存中取
			var bodyDataOfMutualContent = uni.getStorageSync("mutualListSelf");
			// 如果缓存不为空
			if (bodyDataOfMutualContent) {
				this.bodyDataOfMutual.content = bodyDataOfMutualContent;
			}

			this.bodyDataOfMutualLoading = true;

			this.$kt.request.request({
				api: this.$api.business.ovo.userBind.getMutualSelf,
				data: this.requestParamOfMutual,
				stateSuccess: res => {
					if (this.mutualNum == 0) {
						this.bodyDataOfMutual.content = []
					}

					var content = JSON.parse(JSON.stringify(res.data.content));
					// 删除content与bodyDataOfMutual.content相同的元素
					for (var i = 0; i < content.length; i++) {
						for (var j = 0; j < this.bodyDataOfMutual.content.length; j++) {
							if (content[i].ovoUser.id == this.bodyDataOfMutual.content[j].ovoUser.id) {
								content.splice(i, 1);
								i--;
								break;
							}
						}
					}


					this.bodyDataOfMutual.content = this.bodyDataOfMutual.content.concat(content);

					if (this.bodyDataOfMutual.content.length != 0) {
						// 将this.bodyDataOfMutual.content都循环一边，并将item都加一个属性isStorage
						var bodyDataOfMutualContent = JSON.parse(JSON.stringify(this.bodyDataOfMutual.content.slice(0, 100)));
						for (var i = 0; i < bodyDataOfMutualContent.length; i++) {
							bodyDataOfMutualContent[i].isStorage = true;
						}
						uni.setStorageSync("mutualListSelf", bodyDataOfMutualContent);
					}else{
						uni.removeStorageSync("mutualListSelf");
					}


					if (res.data.content.length == 0) {
						this.bodyDataOfMutualEnd = true;
						this.bodyDataOfMutualLoading = false;
						return false;
					}
					if (res.data.content.length < res.data.pageSize) {
						this.bodyDataOfMutualEnd = true;
						this.bodyDataOfMutualLoading = false;
						return false;
					}
					this.mutualNum++;
					this.requestParamOfMutual.pageNumber++;
					this.bodyDataOfMutualLoading = false;
				}
			})

		}

	}
}
</script>

<style lang="scss" scoped>
.ff-box {
	position: relative;
	width: 100%;

	.in-ff-box {
		display: inline-block;
		width: 33%;
		text-align: center;
		box-sizing: border-box;
		font-size: 28rpx;
		color: #999;
	}

	.in-ff-box:active {
		color: #333;
	}
}

.box {
	padding: 20rpx;
	box-sizing: border-box;
}

.push-btn {
	text-align: center;
	padding: 20rpx;
	box-sizing: border-box;
	font-size: 28rpx;
	color: #999;
}

.btn:active {
	opacity: 0.6;
}

.box-of-no-have {
	position: absolute;
	width: 100%;
	top: calc(50% - 100rpx);
	transform: translateY(-50%);
	text-align: center;
	color: #999;
	font-size: 28rpx;
}

.icon-of-no-have {
	width: 120rpx;
	height: 120rpx;
	margin-bottom: 15rpx;
}
</style>
